/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
}

body {
  color: #ffffff;
  font-family: "Rubik Mono One", sans-serif;
  margin: 0;
  font-size: 1vw;
  background: #24186a;
  width: 100%;
}

.container {
