  margin: 0 auto;
  position: relative;
}

.social-block .soc-link {
  border: none;
  background: #f04132;
  width: 2.60417vw;
  height: 2.60417vw;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-block .soc-link svg {
  width: 1.04167vw;
  height: 1.04167vw;
  object-fit: contain;
}

.social-block .soc-link svg * {
  fill: white;
}

.social-block .soc-link:hover {
  background: #cb382e;
}

.social-block .soc-link + .soc-link {
  margin-top: 0.52083vw;
}

.title span {
  color: white;
