  left: 1.30208vw;
  bottom: 1.97917vw;
}

.section_one .maxim {
  display: block;
  width: 12.08333vw;
  position: absolute;
  right: 1.30208vw;
  top: 1.19792vw;
}

.section_one .maxim img {
  width: 100%;
